<script>
    export default {
        name: "ticket-game",
        data() {
            return {
                show: [0, 0, 0, 0, 0, 0],
                number: [0, 0, 0, 0, 0, 0],
                position: 0,
                successMsg: null,
                errorMsg: null
            }
        },
        created() {
            this.position = 0
        },
        computed: {},
        methods: {
            countEmpty() {
                let cnt = 0
                for (let index = 0; index < 6; index++) {
                    cnt += (this.number[index] === 0)
                }
                return cnt
            },
            clearNumber(index) {
                this.number[index] = 0
                this.position = index
                this.update()
            },
            setNumber(num) {
                if (this.number.includes(num)) {
                    this.errorMsg = "Can't select same ball!";
                    setTimeout(() => {
                        this.errorMsg = null
                    }, 2000);
                } else {
                    if (this.position < 6) {
                        this.number[this.position++] = num;
                    }
                    if (this.position === 6) {
                        if (this.countEmpty() === 0) {
                            this.successMsg = "Great! You've selected your 6 numbers"
                            setTimeout(() => {
                                this.successMsg = null
                            }, 1000)
                        }
                    }
                    this.update()
                }

            },
            update() {
                this.show = [];
                for (let index = 0; index < 6; index++) {
                    this.show[index] = this.number[index]
                }
            },
            reset() {
                this.number = [0, 0, 0, 0, 0, 0]
                this.position = 0
                this.update()
            },
            sendData() {
                if (this.countEmpty() > 0) {
                    this.errorMsg = "Please select all 6 numbers"
                    setTimeout(() => {
                        this.errorMsg = null
                    }, 3000);
                } else {
                    this.$store.dispatch('buyTicket', this.number)
                }
            },
            getRandom() {
                var arr = [];
                while (arr.length < 6) {
                    var r = Math.floor(Math.random() * 21) + 1;
                    if (arr.indexOf(r) === -1) arr.push(r);
                }
                this.number = arr;
                this.position = 6;
                this.update()
            }
        }
    }
</script>

